@import url("https://fonts.googleapis.com/css2?family=Mina:wght@400;700&display=swap");
:root {
  --minecraft-font: "Minecraft";
  --mina-regular-font: Mina;
  --mina-weight-regular: 400;
  --mina-weight-bold: 700;

  --background-color: #282c34;
  --text-color: rgb(0, 0, 0);
  --text-active-color: #ffffff;
  --link-color: #61dafb;
  --button-color: #ff0000;
  --button-hover-color: #ffe2e2;
  --button-active-color: #b72020;
  --background-inner-color: #fef0da;
  --background-color: #dc3f3f;
  --background-color2: #ba4141;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: "Minecraft";
  src: url("./fonts/Minecraft.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "MinaR";
  src: url("./fonts/Mina-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "MinaB";
  src: url("./fonts/Mina-Bold.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

body {
  background-color: var(--background-color); /* Red background */
  height: 100vh;
  width: 100vw;

  display: flex;
  justify-content: center;
  align-items: center;
  /* overflow: hidden; */
}
hr {
  width: 100%;
  z-index: 3;
  background: #333;
}

@keyframes gradientMovement {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  /* background: linear-gradient(270deg, #ff7e5f, #feb47b); */
  background: linear-gradient(270deg, #cc2b2b, #ff695e);
  background-size: 200% 200%;
  animation: gradientMovement 8s ease infinite;
}

.background-acc {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background: linear-gradient(270deg, #ff7e5f, #feb47b); */
  background: linear-gradient(270deg, #f06868, #db392d);
  background-size: 200% 200%;
  animation: gradientMovement 8s ease infinite;
}

.background-rej {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background: linear-gradient(270deg, #ff7e5f, #feb47b); */
  background: linear-gradient(270deg, #df2d2d, #880202);
  background-size: 200% 200%;
  animation: gradientMovement 8s ease infinite;
}

@keyframes circularScale {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: transla(1);
  }
}
.background-inner {
  animation: circularFade 5s infinite;
}

.outer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.announcement-container {
  background-color: var(--background-inner-color);
  text-align: center;
}
.announcement-logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.announcement-logo .text-wrapper {
  position: absolute;
  background-color: rgb(255, 255, 255); /* Semi-transparent background */
}
.announcement-logo .text {
  color: #333;
}
.message {
  font-family: MinaR;
  font-weight: 900;
  color: #333;
}
.sub-message {
  font-family: MinaB;
  font-weight: bold;
  color: #333;
}
.email-input {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background-color: #f9f2e9;
  text-align: center;
}
.email-input input[type="email"] {
  border: none;
  outline: none;
  font-family: MinaB;
  background-color: #f9f2e9;
  text-align: center;
}
.email-input::placeholder {
  text-align: center;
  font-size: 15px;
}
.button-search {
  background-color: var(--button-color);
  color: white;
  cursor: pointer;
  font-family: Minecraft;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button-search:hover,
.button-back:hover {
  background-color: #ffffff;
  color: #000000;
}

.email-found {
  font-family: MinaB;
  color: green;
}

.email-not-found {
  font-family: MinaB;
  color: red;
}

.result-message-acc,
.result-message-rej {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: MinaR;
  font-size: 15px;
  width: 100%;
  height: auto;
}

.result-outer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.result-message-acc-h {
  margin-bottom: 0;
  text-align: center;
  margin-top: 10px;
  font-size: 38px;
  color: #c80000;
  font-family: Minecraft;
}
.result-message-rej h1 {
  text-align: center;
  font-size: 38px;
  color: #212121;
  font-family: Minecraft;
}

.result-message-rej p {
  text-align: justify;
}
.unselectable {
  user-select: none; /* Disable text selection */
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
}

.unselectable a {
  color: blue;
  font-family: Minecraft;
  cursor: pointer;
  text-decoration: none;
}
.unselectable a:hover {
  color: red;
  font-family: Minecraft;
  cursor: pointer;
  text-decoration: #dc3f3f;
  text-decoration: underline;
}

@media (max-width: 500px) {
  hr {
    height: 1px;
    margin-top: 15px;
    margin-bottom: 10px;
  }

  .announcement-container {
    max-width: 270px;
    min-width: 100px;
    height: auto;
    width: 100%;

    padding-inline: 40px;
    padding-top: 20px;
    padding-bottom: 40px;
    border-radius: 15px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    border: 5px solid #333;
  }

  .announcement-logo img {
    width: 120px;
    height: auto;
  }
  .announcement-logo .text-wrapper {
    top: 25px;
    font-size: 21px;
  }
  .announcement-logo .text {
    font-size: 20px;
  }
  .message {
    font-size: 12px;
    margin: 20px;
  }
  .sub-message {
    font-size: 15px;
    margin-bottom: 12px;
  }
  .email-input {
    border: 2px solid #333;
    border-radius: 5px;
  }
  .email-input input[type="email"] {
    width: 100%;
    font-size: 14px;
  }
  .button-search {
    border: 2px solid #000000;
    width: 60px; /* Adjusted width */
    height: 40px; /* Adjusted height */
    font-size: 26px; /* Adjusted font size */
    padding-top: 7px;

    transition: transform 0.05s ease, box-shadow 0.05s ease;
    box-shadow: 2px 1px 0px 1px rgb(0, 0, 0);
  }
  .button-search:hover,
  .button-back:hover {
    transform: translate(-3px, -3px);
    box-shadow: 5px 4px 0px 1px rgb(0, 0, 0);
  }

  .button-search:active,
  .button-back:active {
    transform: translate(0px, 0px);
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
    box-shadow: 2px 1px 0px 1px rgb(0, 0, 0);
  }
}

/* Media queries for desktop */
@media (min-width: 501px) {
  hr {
    border: 0;
    height: 2px;
    margin-top: 15px;
    margin-bottom: 10px;
  }

  .announcement-container {
    max-width: 500px;
    min-width: 400px;
    height: auto;
    width: 100%;

    padding-inline: 40px;
    padding-top: 20px;
    padding-bottom: 40px;
    border-radius: 15px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    border: 5px solid #333;
  }

  .announcement-logo img {
    width: 140px;
    height: 60px;
  }
  .announcement-logo .text-wrapper {
    top: 25px;
    font-size: 21px;
  }
  .announcement-logo .text {
    font-size: 20px;
  }
  .message {
    font-size: 17px;
    margin: 20px;
  }
  .sub-message {
    font-size: 18px;
    margin-bottom: 12px;
  }
  .email-input {
    border: 2px solid #333;
    border-radius: 5px;
  }
  .email-input input[type="email"] {
    width: 100%;
    font-size: 14px;
  }
  .button-search {
    border: 2px solid #000000;
    width: 70px; /* Adjusted width */
    height: 50px; /* Adjusted height */
    font-size: 26px; /* Adjusted font size */
    padding-top: 7px;

    transition: transform 0.05s ease, box-shadow 0.05s ease;
    box-shadow: 2px 1px 0px 1px rgb(0, 0, 0);
  }
  .button-search:hover,
  .button-back:hover {
    transform: translate(-3px, -3px);
    box-shadow: 5px 4px 0px 1px rgb(0, 0, 0);
  }

  .button-search:active,
  .button-back:active {
    transform: translate(0px, 0px);
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
    box-shadow: 2px 1px 0px 1px rgb(0, 0, 0);
  }
}
